#weatherBox_wr {

  width: 100vw;
  height: 100vh;
  position: relative;

  background-color: burlywood;
  
  #weatherBox {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem 2.5rem;
    width: 600px;

    border: 2px solid #fff;
    border-radius: 20px;

    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    .weatherBox_info {
      div:not(:first-child) {
        margin-top: 20px;
      }

      p {
        font-size: 2rem;
        color: #fff;
      }
    }

    .weatherBox_icon {
      font-size: 4rem;
    }
  }
}

@media all and (max-width:768px) {

  #weatherBox_wr {
    
    #weatherBox {
      width: calc(100% - 32px);
    }
  }
  
}